// impporting  components
import React from 'react';
import Typewriter from 'typewriter-effect';
//importing Image
import Profile from "../assets/my/my.jpg"

// Importing  css
import Style from "../css/Home.module.css";

const Home = () => {
    return (
        <div className={Style.content__section}>
            <div className={Style.content__container}>
                <div className={Style.profile}>
                    <img src={Profile} alt="Developer Pic" />
                </div>
                <div className={Style.profile__descrption}>
                    <h2>Hello, I am Shubham Rawat, I am</h2>
                    <Typewriter options={{
                        strings: ['Full Stack Developer', 'Graphics Desginer', 'Front-End Developer', 'Back-End Developer', 'React Developer'],
                        autoStart: true,
                        loop: true,
                        delay: 20,
                    }} />
                </div>
            </div>
        </div>
    );
}

export default Home;