//Importing Component
import React from "react";

// Import css

import "../css/Base.css";
import Style from "../css/Card.module.css";


const Card = (props) => {
    console.log(props);
    return (
        <div className={Style.card__container} data-title={props.title}>
            <div className={Style.card__img__container}>
                <img src={props.src} alt={props.alt} />
            </div>
        </div>
       
    );
}

export default Card;