// importing Component
import React from "react";

// Importing Css

import "../css/Base.css";
import Style from "../css/ServiceCard.module.css";

class ServiceCard extends React.Component {

    render() {
        return (
            <div className={Style.servce__card}>
                <div className={Style.service__body}>
                    <div className={Style.service__image}>
                        <i className={this.props.icon}></i>
                    </div>
                    <div className={Style.services__heading}>
                        <h3>{this.props.heading}</h3>
                    </div>

                    <div className={Style.service__content}>
                        <p>{this.props.content}</p>
                    </div>
                </div>
            </div>
        )
    }
}

export default ServiceCard;