// impporting  components
import React from 'react'
import Sidebar from "./Sidebar";
import Home from "./Home.js";
import PageNotFound from './PageNotFound';
import Feedback from './Feedback';
import Services from './Services';
import Portfolio from './Portfolio';
import Contact from './Contact';
import { BrowserRouter, Switch, Route } from "react-router-dom";

// impporting  css
import "../css/Base.css";
import Style from "../css/Main.module.css"

const Main = () => {

    return (
        <BrowserRouter>
            <div className={Style.main}>
                <Sidebar />
                {/* Routes */}
                <Switch>
                    <Route path="/" exact={true} component={Home} />
                    <Route path="/feedback" exact={true} component={Feedback} />
                    <Route path="/services" exact={true} component={Services} />
                    <Route path="/portfolio" exact={true} component={Portfolio} />
                    <Route path="/contact-me" exact={true} component={Contact} />
                    <Route component={PageNotFound} />
                </Switch>
            </div>
        </BrowserRouter>

    )

}

export default Main;