// impporting  components
import React from 'react';
import { NavLink } from "react-router-dom";

// importing css
import Style from "../css/Sidebar.module.css";

const Sidebar = () => {
    return (
        <div className={Style.sidebar__section}>
            <div className={Style.brand__container}>
                <div className={Style.brand__name}>SR</div>
            </div>
            <div className={Style.navigation__container}>
                <NavLink to="/" activeClassName="is__active" className={Style.nav__item}><i className="fa fa-home"></i></NavLink>
                <NavLink to="/services" activeClassName="is__active" className={Style.nav__item}><i className="fas fa-cog"></i></NavLink>
                <NavLink to="/portfolio" activeClassName="is__active" className={Style.nav__item}><i className="fas fa-suitcase"></i></NavLink>
                <NavLink to="/contact-me" activeClassName="is__active" className={Style.nav__item}><i className="fas fa-mobile-alt"></i></NavLink>
                <NavLink to="/feedback" activeClassName="is__active" className={Style.nav__item}><i className="fas fa-info-circle"></i></NavLink>
            </div>

        </div>
    );
}

export default Sidebar;