// importing Components 
import React from "react";
import Card from "../component/Card";

// Importing Images 
import First from "../assets/portfolio/pexels-bruno-scramgnon-5007442.jpg";
import Second from "../assets/portfolio/pexels-dapo-abideen-3535630.jpg";
import Third from "../assets/portfolio/pexels-jonathan-borba-3727771.jpg";
import Fourth from "../assets/portfolio/pexels-maria-eduarda-loura-magalhães-tavares-4340053.jpg";
import Fifth from "../assets/portfolio/pexels-marko-blazevic-1327405.jpg";
import Sixth from "../assets/portfolio/pexels-yo-choowa-4594134.jpg";

// importing css
import Style from "../css/Portfolio.module.css";

const Portfolio = () => {
    return (
        <div className={Style.content__section}>
            <div className={Style.content__container}>
                <h2>Portfolio</h2>

                <div className={Style.portfolio__container}>
                    <Card src={First} title="First" alt="First" />
                    <Card src={Second} title="Second" alt="Second" />
                    <Card src={Third} title="Third" alt="Third" />
                    <Card src={Fourth} title="Fourth" alt="Fourth" />
                    <Card src={Fifth} title="Fifth" alt="Fifth" />
                    <Card src={Sixth} title="Sixth" alt="Sixth" />
                </div>
            </div>
        </div>
    );
}

export default Portfolio;